@font-face {
  font-family: "Montserrat-Regular";
  src: url("../fonts/Montserrat/Montserrat-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-display: fallback;
}

@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik/rubik-bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Rubik/rubik-bold.woff2") format("woff2"), url("../fonts/Rubik/rubik-bold.woff") format("woff"), url("../fonts/Rubik/rubik-bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  background-color: #fdf9ff;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "Montserrat-Regular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input,
body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid,
body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

input,
textarea,
select {
  font-family: "Montserrat-Regular", sans-serif;
}

.container {
  margin: auto;
  width: 100%;
  padding-left: 30px;
  padding-right: 30px;
}

.aside_left {
  width: 270px;
  background-color: #fdf9ff;
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  border-right: 1px solid #e8dfec;
  padding: 30px 20px;
  transition: all 0.3s ease;
  overflow-y: auto;
  display: flex;
  flex-wrap: wrap;
}

.section {
  background-color: #f2f2fc;
  min-height: 100vh;
  padding: 0 30px;
  position: fixed;
  left: 270px;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  opacity: 1;
  overflow-x: hidden;
  overflow-y: auto;
  transition: all 0.3s ease;
}

.section.active {
  z-index: 2;
  opacity: 1;
  animation: slideSection 1s ease;
}

.section.back-section {
  z-index: 1;
}

@keyframes slideSection {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}

.logo {
  padding: 0;
}

.logo a {
  font-size: 40px;
  color: #302e4d;
  font-family: 'Rubik', sans-serif;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
  position: relative;
  padding: 0 10px;
}

.logo a:before,
.logo a:after {
  content: '';
  height: 20px;
  width: 20px;
  position: absolute;
}

.logo a:before {
  top: 0;
  left: 0;
}

.logo a:after {
  bottom: 0;
  right: 0;
}

.aside_left .nav {
  margin: 80px 0 0 0;
  list-style: none;
}

.aside_left .nav li {
  display: block;
}

.aside_left .nav li a {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #302e4d;
  text-decoration: none;
  line-height: 45px;
  border-bottom: 1px solid #e8dfec;
  transition: all .3s ease;
}

.aside_left .nav li a:not(.active):hover {
  padding-left: 5px;
}

.copyright_text {
  margin-top: auto;
}

.copyright_text ul li a,
.copyright_text {
  color: #7d7d7d;
  font-size: 13px;
  transition: all .3s ease;
}

.copyright_text ul li a:hover {
  text-decoration: none;
}

.copyright_text ul {
  padding: 0;
  list-style: none;
}

.aside_left .nav li a .fa {
  margin-right: 5px;
}

.aside_left .nav-toggler {
  width: 40px;
  height: 40px;
  border: 1px solid #d4d4e3;
  position: fixed;
  left: 300px;
  top: 30px;
  z-index: 11;
  cursor: pointer;
  background-color: #fdf9ff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  display: none;
}

.aside_left .nav-toggler span {
  height: 2px;
  width: 16px;
  display: inline-block;
  position: relative;
}

.aside_left .nav-toggler.open span {
  background-color: transparent;
}

.aside_left .nav-toggler span:after,
.aside_left .nav-toggler span:before {
  content: '';
  height: 2px;
  width: 18px;
  position: absolute;
  top: -6px;
  left: 0;
}

.aside_left .nav-toggler span:after {
  top: 6px;
}

.aside_left .nav-toggler.open span:before {
  transform: rotate(45deg);
  top: 0;
}

.aside_left .nav-toggler.open span:after {
  transform: rotate(-45deg);
  top: 0;
}

