.lightbox {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: zoom-out;
  padding: 30px;
  display: none;
}

.lightbox.open {
  display: flex;
}

.lightbox .lightbox-content img {
  height: auto;
  width: auto;
  max-width: 100%;
  max-height: 450px;
  cursor: pointer;
  display: block;
  padding: 30px 0;
}

.lightbox .lightbox-content {
  position: relative;
}

.lightbox.open .lightbox-content {
  animation: lightboxImage .5s ease;
}

@keyframes lightboxImage {
  0% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

.lightbox .lightbox-content .lightbox-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  z-index: -1;
  padding: 5px 10px;
}

.lightbox .lightbox-content .close-lightbpx {
  position: absolute;
  height: 30px;
  width: 30px;
  line-height: 30px;
  top: 0;
  right: 0;
  font-size: 32px;
  text-align: right;
  color: #fff;
}

.lightbox .lightbox-controls .prev-item,
.lightbox .lightbox-controls .next-item {
  height: 40px;
  width: 40px;
  background-color: #fff;
  text-align: center;
  cursor: pointer;
  z-index: 110;
  transition: all .3s ease;
}

.lightbox .lightbox-controls .prev-item:active,
.lightbox .lightbox-controls .next-item:active {
  transform: scale(1.1);
}

.lightbox .lightbox-controls .prev-item .fa,
.lightbox .lightbox-controls .next-item .fa {
  font-size: 32px;
  line-height: 40px;
}

