  font-size: 12px;
  font-weight: 500;
  margin-bottom: 5px;
  color: #949191;
}

.col_page_item .mobile_sec .radio-label .price_mobile_output,
.price-form_item .radio .radio-label .price_mobile_output {
  display: block;
  margin-left: 37px;
  white-space: nowrap;
}

.radio {
  margin: 0.5rem 0.5rem 0.5rem 0;
  width: 100%;
}

.form-group input[type='checkbox'],
.style-switcher input[type='radio'],
.mobile_sec input[type='checkbox'],
.radio input[type='radio'] {
  position: absolute;
  opacity: 0;
}

.form-group input[type='checkbox'] + .css-label:before,
.style-switcher input[type='radio'] + .radio-label:before,
.mobile_sec input[type='checkbox'] + .radio-label:before,
.radio input[type='radio'] + .radio-label:before {
  content: '';
  background: #fff;
  border-radius: 100%;
  display: inline-block;
  width: 1.1rem;
  height: 1.1rem;
  position: relative;
  top: 5px;
  margin-right: 1em;
  vertical-align: top;
  cursor: pointer;
  text-align: center;
  -webkit-transition: all 250ms ease;
  transition: all 250ms ease;
}

.style-switcher input[type='radio'] + .radio-label:before {
  left: 9px;
  top: 2px;
}

.mobile_ad input[type='checkbox'] + .radio-label:before {
  top: 6px;
}

.form-group input[type='checkbox'] + .css-label:before {
  top: 1px;
}

.form-group input[type='checkbox']:checked + .css-label:before,
.style-switcher input[type='radio']:checked + .radio-label:before,
.mobile_sec input[type='checkbox']:checked + .radio-label:before,
.radio input[type='radio']:checked + .radio-label:before {
  box-shadow: inset 0 0 0 4px #fff;
}

.form-group input[type='checkbox']:focus + .css-label:before,
.style-switcher input[type='radio']:focus + .radio-label:before,
.mobile_sec input[type='checkbox']:focus + .radio-label:before,
.radio input[type='radio']:focus + .radio-label:before {
  outline: none;
}

